const detectCollisionPoint = (balanceLineTopPosition, rotationDegreeOfBalance, positionProbability, balanceSide = 'left') => {
    let collisionPoint = balanceLineTopPosition;
    const positionYDelta = Math.abs(Math.sin(degrees_to_radians(rotationDegreeOfBalance)) * 500);
    const increase = parseInt(balanceLineTopPosition) + (positionYDelta * (1 - positionProbability));
    const decrease = parseInt(balanceLineTopPosition) - (positionYDelta * (1 - positionProbability));
    if (balanceSide === 'left') {
        if (rotationDegreeOfBalance > 0) {
            collisionPoint = decrease;
        } else {
            collisionPoint = increase;
        }
    } else {
        if (rotationDegreeOfBalance > 0) {
            collisionPoint = increase;
        } else {
            collisionPoint = decrease;
        }
    }
    return collisionPoint;
}

const degrees_to_radians = (degrees) => {
    return degrees * (Math.PI / 180);
}

export {detectCollisionPoint};
