<template>
  <div class="info-container">
    <div>
      <p>Total Weight: {{weight.totalLeftWeight}}</p>
      <p>Momentum: {{force.leftForce}}</p>
    </div>
    <div>
      <p>Total Weight: {{weight.totalRightWeight}}</p>
      <p>Momentum: {{force.rightForce}}</p>
    </div>
  </div>
</template>

<script>

import {computed} from "vue";
import {useStore} from "vuex";

export default {
  name: 'GameInfo',
  props: {
    msg: String
  },

  setup() {
    const store = useStore();
    const weight = computed(_ => store.getters['main/getTotalWeight']);
    const force = computed(_ => store.getters['main/getMomentum']);
    return {weight, force}
  }
}
</script>

<style scoped>
.info-container {
  width: 100%;
  position: absolute;
  top: 15px;
  display: flex;
  justify-content: space-around;
  height: 40px;
}
</style>
