<template>
  <div
      v-bind:class="type"
      v-bind:style="{
        width: boxWidth + 'px',
        height: boxWidth + 'px',
        top: positionY + 'px',
        left: side === 'left' ? positionX + 'px' : 'initial',
        right: side === 'right' ? positionX + 'px': 'initial'
      }"
      class="box-container">
    <span class="weight-text">{{ weight }}</span>
    <img v-if="type === 'triangle'" style="width: 100%" :src="triangleImage">
  </div>
</template>

<script>

import {computed, ref, toRef, toRefs, watch} from "vue";
import {useStore} from "vuex";
import {detectCollisionPoint} from "@/helpers/detectCollisionPoint";

export default {
  name: 'TheWeight',
  props: {
    index: Number,
    type: {
      type: String,
      default: 't'
    },
    weight: {
      type: Number,
      default: 1
    },
    positionY: {
      type: Number,
      default: 1
    },
    isFalling: Boolean,
    positionProbability: {
      type: Number,
      default: 1
    },
    side: String
  },

  setup(props) {
    const store = useStore();
    const boxWidth = ref((props.weight * 8) + 60);
    const balanceSideLimits = computed(_ => store.getters['main/getBalanceLimits'])
    const positionX = computed(_ => balanceSideLimits.value + (500 * props.positionProbability))

    const rotationDegreeOfBalance = computed(_ => store.getters['balance/getRotationAngle'])
    const balanceLineTopPosition = computed(_ => store.getters['balance/getBalanceTopPosition'])

    watch(() => ref(props.positionY), (first, second) => {
      const collisionPint = detectCollisionPoint(balanceLineTopPosition.value, rotationDegreeOfBalance.value, props.positionProbability, props.side);
      if (collisionPint < props.positionY + boxWidth.value && props.isFalling) {
          store.dispatch(props.side === 'left'? 'main/updateWeightFalling' : 'main/updateRightWeightFalling', {index: props.index, isFalling: false})
      }
    });

    const triangleImage = '/triangle.png'
    return {boxWidth, balanceSideLimits, positionX, triangleImage};
  },
}

</script>

<style scoped>
.box-container {
  position: absolute;
  display: flex;
  justify-content: center;
  top: 0;
  width: 60px;
  height: 60px;
}

.circle {
  border-radius: 50%;
  background: #2d2df8;
}
.triangle {
}

.rectangle {
  background-color: green;
}

.weight-text {
  text-align: center;
  position: absolute;
  top: -20px;
  font-size: 20px;
}
</style>
