<template>
  <div class="info-container">
    <button @click="setGameState(true)" >Pause</button>
    <button @click="setGameState(false)">Continue</button>
  </div>
</template>

<script>

import {computed} from "vue";
import {useStore} from "vuex";

export default {
  name: 'TheControls',
  props: {
    msg: String
  },

  setup() {
    const store = useStore();
    const weight = computed(_ => store.getters['main/getTotalWeight']);
    const force = computed(_ => store.getters['main/getMomentum']);

    const setGameState = (state) => {
      console.log(state);
      store.dispatch('main/setGameState', state)
    }
    return {weight, force, setGameState}
  }
}
</script>

<style scoped>
.info-container {
  width: 100%;
  position: absolute;
  top: 35px;
  display: flex;
  justify-content: flex-end;
  height: 40px;
}
</style>
