<template>
  <div ref="scene" class="scene">
    <TheControls/>
    <GameInfo/>
    <TheWeight
        :key="index"
        v-for="(weight, index) in weights"
        :index="index"
        :type="weight.type"
        :isFalling="weight.isFalling"
        :weight="weight.weight"
        :positionY="weight.positionY"
        :positionProbability="weight.horizontalPositionProbability"
        :side="'left'"
    ></TheWeight>
    <TheWeight
        :key="index"
        v-for="(weight, index) in rightWeights || []"
        :index="index"
        :type="weight.type"
        :isFalling="weight.isFalling"
        :weight="weight.weight"
        :positionY="weight.positionY"
        :positionProbability="weight.horizontalPositionProbability"
        :side="'right'"
    ></TheWeight>
    <TheBalance ref="balanceTemplateRef"/>
  </div>
</template>

<script>
import GameInfo from "@/components/TheInfo";
import TheBalance from "@/components/TheBalance";
import TheWeight from "@/components/BaseWeight";
import {useStore} from "vuex";
import {computed, onMounted, ref} from "vue";
import {detectCollisionPoint} from "@/helpers/detectCollisionPoint";
import {calculateRotationDelta} from "@/helpers/calculateRotationDelta";
import TheControls from "@/components/TheControls";
import {listenKeyboradEvents} from "@/helpers/keyboardEvents";

export default {
  name: 'TheScene',
  components: {TheWeight, GameInfo, TheBalance, TheControls},
  props: {
    msg: String
  },

  setup() {
    let speed = 4; // fall speed of weights .There is no acceleration
    const store = useStore();
    const scene = ref(null)

    let isGamePaused = computed(_ => store.getters['main/getGameState']);
    const weights = computed(_ => store.getters['main/getWeights']);
    const rightWeights = computed(_ => store.getters['main/getRandomWeights']);
    const rotationDegreeOfBalance = computed(_ => store.getters['balance/getRotationAngle'])
    const balanceLineTopPosition = computed(_ => store.getters['balance/getBalanceTopPosition'])

    const pullLeft = (status) => { // exeutes when user click to left arrow
      store.dispatch('main/setIsArrowLeftDown', status)
    }
    const pullRight = (status) => { // exeutes when user click to right arrow
      store.dispatch('main/setIsArrowRightDown', status)
    }

    // add keyboard event listeners
    listenKeyboradEvents(pullLeft, pullRight);

    onMounted(() => {
      // get side limits of balance for correcting weights x position
      const balanceSideLimit = (scene.value.clientWidth - 1000) / 2;
      store.dispatch('main/setBalanceSideLimit', {balanceSideLimit});

      const animationTick = () => {
        if(isGamePaused.value) return requestAnimationFrame(animationTick);
        // animate left weights falling
        for (let index = 0; index < weights.value.length; index++) {
          if (weights.value[index].isFalling) {
            store.dispatch('main/updateWeightPosition', {index, speed: speed})
          }
        }
        // animate right weights falling
        for (let index = 0; index < rightWeights.value.length; index++) {
          if (rightWeights.value[index].isFalling) {
            store.dispatch('main/updateRightWeightPosition', {index, speed: speed})
          }
        }

        // move fallen right weights relative to balance line
        let totalRightWeight = 0; //reset calculated total weight for this cyclie
        for (let index = 0; index < rightWeights.value.length; index++) {
          if (!rightWeights.value[index].isFalling) {

            // calculateTotalRightWeight
            totalRightWeight += rightWeights.value[index].weight; // calculate fallen boxes total weight
            store.dispatch('main/setTotalRightWeight', totalRightWeight) //store total weight
            //
            // calculates boxes position related to blanace line
            let position = detectCollisionPoint(balanceLineTopPosition.value, rotationDegreeOfBalance.value, rightWeights.value[index].horizontalPositionProbability, 'right')
            position -= 8 * rightWeights.value[index].weight + 60; // calculate collision point of box with balance line related to box volume
            // save position of each fallen box
            store.dispatch('main/setFalledRightWeightPosition', {index, position})
          }
        }

        // move fallen left weights relative to balance line
        let totalLeftWeight = 0;
        for (let index = 0; index < weights.value.length; index++) {
          if (!weights.value[index].isFalling) {

            // calculateTotalLeftWeight
            totalLeftWeight += weights.value[index].weight;
            store.dispatch('main/setTotalLeftWeight', totalLeftWeight)
            //
            let position = detectCollisionPoint(balanceLineTopPosition.value, rotationDegreeOfBalance.value, weights.value[index].horizontalPositionProbability, 'left')
            position -= 8 * weights.value[index].weight + 60;
            store.dispatch('main/setFalledWeightPosition', {index, position})
          }
        }

        // move weight to left or right
        const isLeftArrowDown = store.getters['main/getIsArrowLeftDown'];
        const isRightArrowDown = store.getters['main/getIsArrowRightDown'];
        if(isLeftArrowDown || isRightArrowDown) {
          for(let i = 0; i <  weights.value.length; i++) {
            const weight = weights.value[i];
            if(weight.isFalling) {
              isLeftArrowDown? store.dispatch('main/moveLeft', i) : store.dispatch('main/moveRight', i);
            }
          }
        }


        // get calculated side forces
        const force = store.getters['main/getMomentum']
        // calculate the balance line rotation speed or delta
        const rotationDelta = calculateRotationDelta(force.rightForce, force.leftForce)
        // set delta to state
        store.dispatch('balance/setRotationDelta', rotationDelta)

        // rotate balance
        store.dispatch('balance/setBalanceRotationDeg')

        // check if rotation degree more than 30 deg
        if (store.getters['balance/getRotationAngle'] > 30 || store.getters['balance/getRotationAngle'] < -30 ) {
          store.dispatch('main/setGameState', true);
          alert('Game Over!')
        }
        requestAnimationFrame(animationTick);
      }
      requestAnimationFrame(animationTick);
    })
    return {scene, store, weights, rightWeights, pullRight, pullLeft}
  }
}
</script>

<style scoped>
.scene {
  height: 100vh;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>
