<template>
  <div class="balance">
    <div class="space"></div>
    {{rotationAngle}}
    <div v-bind:style="{'transform': `rotate(` + rotationAngle + 'deg)'}" ref="balanceLine" class="line"></div>
    <div class="balance-bottom"></div>
  </div>
</template>

<script>

import {computed, onMounted, ref} from "vue";
import {useStore} from "vuex";

export default {
  name: 'TheBalance',
  props: {
    msg: String
  },

  setup () {
    const store = useStore()
    const balanceLine = ref(null)
    const rotationAngle = computed(_ => store.getters['balance/getRotationAngle'])
    onMounted(() => {
      store.dispatch('balance/setBalanceLineTopPosition', balanceLine.value.getBoundingClientRect().top)
    })
    return {balanceLine, rotationAngle}
  }
}
</script>

<style scoped>
.balance {
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
}

.space {
  height: 500px;
}

.balance-bottom {
  width: 1px;
  height: 0;
  border-left: 25px solid transparent;
  border-right: 25px solid transparent;
  border-bottom: 75px solid black;
}

.line {
  width: 1000px;
  border-bottom: 4px solid red;
}
</style>
