const calculateRotationDelta = (rightForce, leftForce) => {
    if (rightForce > leftForce && leftForce !== 0) {
        return rightForce / leftForce * 0.05;
    } else if (rightForce < leftForce && rightForce !== 0) {
        return leftForce / rightForce * -0.05;
    }
    return 0;
}

export {calculateRotationDelta}
